html {
  background-color: dimgrey;

  .App {
    border: 2px solid aqua;
    border-radius: 5px;
    width: 300px;
    height: 200px;
    margin: 150px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    .field {
      display: flex;
      justify-content: center;

      input {
        font-size: 30px;
        font-weight: bold;
        outline: 1px transparent;
        border: none;
        border-radius: 5px;
        background-color: aqua;
        width: 275px;
        height: 80px;
        text-align: center;
      }
    }

    .btnArea {
      padding: 10px;
      margin: 10px;
      border: 2px solid aqua;
      border-radius: 5px;
      display: flex;
      justify-content: space-around;

      .error {
        opacity: 0.5;

      }

      .notError {
        opacity: 1;
      }

      button {
        border-radius: 5px;
        border: none;
        width: 100px;
        height: 50px;
        font-size: 30px;
        font-weight: bold;
        color: black;
        background-color: aqua;
        outline: 1px transparent;
      }
    }
  }
}


